import Interceptor from './core/interceptor';
import Request from './index';
import jwtToken from './../../utils/jwt.js';
// import TokenApi from '../.././../api/token';

export const globalInterceptor = {
	request: new Interceptor(),
	response: new Interceptor()
};

/**
 * 
 *  
 * `content-type`  application/json
 * header `content-type`    catch
 */
export const config = {
	baseURL: 'http://school.test/api',
	// baseURL: 'https://www.yuanhy.com/api',
	// dataType: 'json',
	// responseType: 'text',
	header: {
		// uid: 'xxxx',
		contentType: 'application/x-www-form-urlencoded'
		// 'Content-Type': 'application/json'
	}
};

/**
 *  
 * : token
 *
 * `return config` 
 * `return false` catch
 * `return Promise.reject('xxxxx')` , catch
 *
 * @param {Object} config 
 */
globalInterceptor.request.use(
	(config) => {
		// console.log('is global request interceptor 1', config);
		if (jwtToken.getToken()) {
			config.header.Authorization = 'Bearer ' + jwtToken.getToken();
		}
		// getToken() && (config.header.token = getToken());

		return config;
		// return false;
		// return Promise.reject('is error')
	},
	(err) => {
		// console.error('is global fail request interceptor: ', err);
		return false;
	}
);

