/**
 *  
 * : 
 *
 * `return res` 
 * `return false` catch
 * `return Promise.reject('xxxxx')` , catch
 *
 * @param {Object} res 
 * @param {Object} config 
 * @return {Object|Boolean|Promise<reject>}
 */
globalInterceptor.response.use(
	(res, config) => {
		// console.log('is global response interceptor');

		//  code
		if (!(res.data && res.data.code)) {
			return res;
		}

		// codehttp
		const code = parseInt(res.data.code);

		// 20x ~ 30x
		if (200 <= code && code < 400) {
			return res;
		} else if (code == 401 && config.count === 0) {
			// token , 
			config.count++;
			config.url = config.instanceURL;
			return getApiToken(2460392754)
				.then(saveToken)
				.then(() => Request().request(config));
		} else {
			return Promise.reject(res, config);
		}

		// return false;
		// return Promise.reject('is error')
	},
	(err, config) => {
		// console.error('is global response fail interceptor');
		// console.error('err: ', err);
		// console.error('config: ', config);
		const {
			errMsg,
			data
		} = err;

		return Promise.reject({
			errMsg,
			data,
			config
		});
	}
);

//  token
async function getApiToken(uid) {
	const res = await TokenApi.getMockToken(uid);
	const {
		token
	} = res.data;

	return token;
}

//  localStorage  token
function getToken() {
	return uni.getStorageSync('token');
}

//  token  localStorage
function saveToken(token) {
	uni.setStorageSync('token', token);
}
